import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { API } from '../../app.config';
import { Observable, Subject, map, tap } from 'rxjs';
import { Branches, WebStorageService } from './webStorage.service';

@Injectable({
  providedIn: 'root',
})
export class QuotesService {
  // private quotes$: Subject<any> = new Subject<any>();
  // messageQuote$: Observable<any> = this.quotes$.asObservable();

  quotesArr: any[] = [];
  constructor(
    private http: HttpClient,
    @Inject(API) private baseUrl: string,
    private webStorageService: WebStorageService
  ) {}

  getBranchesSelected(): Branches {
    return this.webStorageService.getBranches();
  }
  //to-do refractor get quotes
  getInitQuote(): Observable<any> {
    return this.http
      .post<any>(`${this.baseUrl}/initQuote`, {
        branchesSelected: this.getBranchesSelected(),
      })
      .pipe(
        map((quote) => {
          return quote;
        })
      );
  }

  getAdditionalQuote(): Observable<any> {
    return this.http
      .post<any>(`${this.baseUrl}/additionalQuote`, {
        branchesSelected: this.getBranchesSelected(),
      })
      .pipe(
        map((quote) => {
          return quote;
        })
      );
  }

  getSingleQuote(): Observable<any> {
    return this.http
      .post<any>(`${this.baseUrl}/singleQuote`, {
        branchesSelected: this.getBranchesSelected(),
      })
      .pipe(
        map((quote) => {
          return quote;
        })
      );
  }
}
