import {
  AfterViewInit,
  CUSTOM_ELEMENTS_SCHEMA,
  ChangeDetectorRef,
  Component,
  ElementRef,
  OnInit,
  ViewChild,
} from '@angular/core';

import { Observable, map, tap } from 'rxjs';
import { QuotesService } from '../../shared/services/quotes.service';
import { CommonModule } from '@angular/common';
import { SwiperContainer } from 'swiper/element/bundle';
import { LoadingComponent } from '../../shared/component/loading/loading.component';
import { SvgQuoteComponent } from '../../shared/component/svg-quote/svg-quote.component';

@Component({
  selector: 'app-quotes-container',
  standalone: true,
  imports: [CommonModule, LoadingComponent, SvgQuoteComponent],
  schemas: [CUSTOM_ELEMENTS_SCHEMA],
  templateUrl: './quotes-container.component.html',
  styleUrl: './quotes-container.component.less',
})
export class QuotesContainerComponent implements OnInit, AfterViewInit {
  // readonly quoteMessage$: Observable<any> = this.quotesService.messageQuote$;

  @ViewChild('swiper') swiperRef!: ElementRef<SwiperContainer>;
  isLoading: boolean = true;
  isAddLoading: boolean = false;
  showAddLoading: boolean = false;
  tryAgainQuoteActive: boolean = false;
  readonly INIT_QUOTE_NUM = 4;
  readonly ADD_QUOTE_NUM = 2;
  readonly MAX_QUOTE_NUM = 20;

  constructor(
    private quotesService: QuotesService,
    private cd: ChangeDetectorRef
  ) {}
  ngOnInit(): void {
    //TO-DO servizio custom numero di quote iniziali
  }
  ngAfterViewInit(): void {
    this.swiperRef.nativeElement.swiper.on('slideChange', (swiper) => {
      if (swiper.activeIndex === swiper.slides.length - 1) {
        this.additionalQuote();
      }
      if (swiper.activeIndex === swiper.slides.length - 2) {
        this.showAddLoading = true;
      }
    });

    this.initQuote();
  }

  initQuote() {
    this.quotesService.getInitQuote().subscribe((quotes) => {
      this.renderSwiper(quotes);
    });
  }

  additionalQuote() {
    this.isAddLoading = true;
    this.quotesService.getAdditionalQuote().subscribe((quotes) => {
      this.renderSwiper(quotes);
    });
  }

  tryAgainQuote() {
    this.quotesService.getSingleQuote().subscribe((quotes) => {
      this.renderSwiper(quotes, true);
    });
  }

  handleCapSlide() {
    if (
      this.swiperRef.nativeElement.swiper.slides.length >= this.MAX_QUOTE_NUM
    ) {
      this.swiperRef.nativeElement.swiper.removeSlide([0, 1]);
    }
  }

  renderSwiper(quotes: any, isTryingAgain: boolean = false) {
    try {
      let quoteContentList = JSON.parse(quotes.result);
      quoteContentList.forEach((quoteContent: any) => {
        // let quoteContent = JSON.parse(quote.content);
        this.swiperRef.nativeElement.swiper.appendSlide(
          `<swiper-slide>
          <div id="quote-content" class="card mx-2 text-white">
            <div class="card-body">
            <blockquote
            class="max-w-4xl mx-auto p-6 text-center italic rounded-lg cursor-pointer "
          >

            <p class="text-lg sm:text-lg md:text-2xl lg:text-3xl xl:text-4xl  ">
            ${quoteContent.quote} 
            </p>
                  <cite class="not-italic font-normal text-sm sm:text-md md:text-lg lg:text-xl xl:text-2xl"
              >~ ${quoteContent.exponent || quoteContent.character} ${
            quoteContent.opera?.length > 0 ? ` from ${quoteContent.opera}` : ``
          } ~</cite>
            
          </blockquote>
          
            </div>
          </div>
        </swiper-slide>`
        );
      });
    } catch (error) {
      console.error(error);
      if (!isTryingAgain && this.tryAgainQuoteActive) {
        this.tryAgainQuote();
      }
    }
    this.handleCapSlide();
    this.isLoading = false;
    this.isAddLoading = false;
    this.showAddLoading = false;
  }
}
